@import '../../../base';

.loading-screen-v2 {
  background: linear-gradient(150.14deg, $color-input-form-landing-desc 0.6%, $color-loading 100%);
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  opacity: 0;

  transition: z-index 0.3s step-end, opacity 0.3s linear;

  &.loading-screen-active {
    opacity: 0.9;
    z-index: 1051;
    transition: z-index 0.3s step-start, opacity 0.3s linear;
  }
  

  .spinner-animation {
    top: calc(45% - 40px);
    $spinner-height: 60px;
    width: $spinner-height !important;
    height: $spinner-height !important;

    .dot1,
    .dot2 {
      width: $spinner-height * 0.5 !important;
      height: $spinner-height * 0.5 !important;
      background-color: $color-form !important;
    }
  }

  .dot1, .dot2 {
    background-color: $color-form !important;
  }

  .bottom-container {
    position: absolute;
    top: 50%;
    width: 230px;
    left: calc(50% - 115px);
    overflow-x: hidden;
    overflow-y: hidden;

    p {
      font-weight: $font-weight-bold;
      font-size: $font-size-tiny;
      color: white;
      text-align: center;
      margin: 0;
    }
  }

  .logo-container {
    display: flex;

    img {
      height: 64px;
      width: 64px;
      margin: 0 10px;
    }
  }
}

@keyframes animate-active {
  0% {
    opacity: 0;
    z-index: -1;
  }
  1% {
    z-index: 9999;
  }
  100% {
    opacity: 0.9;
  }
}

@keyframes animate-active {
  0% {
    opacity: 0.9;
    z-index: 9999;
  }
  1% {
    opacity: 0;
  }
  100% {
    z-index: -1;
  }
}

.loading-modal {
  max-width: 480px;

  .modal-body {
    padding-top: 0;
    padding-bottom: 0;
  }

  .modal-footer {
    button {
      margin: auto;
    } 
  }
}