@import "../../base";

.moovaz-container {
  .title {
    font-weight: $font-weight-heavy;
    font-size: $font-size-small + $font-size-tiny;
    color: $color-input-text;
    text-align: left;
  }

  .desc {
    font-weight: $font-weight-semibold;
    font-size: $font-size-smallest;
    color: $color-input-form-landing-desc2;
  }

  .blue {
    font-weight: $font-weight-bold;
    font-size: $font-size-regular;
    color: $color-input-form-landing-desc;
  }

  .icon-text {
    font-weight: $font-weight-bold;
    font-size: $font-size-tiny;
    text-align: center;
    color: $color-input-text;
  }

  .checkbox-input-desc {
    font-weight: $font-weight-normal;
    font-size: $font-size-smallest;
    color: $color-input-form-landing-desc2;
  }

  .main-img {
    img {
      width: 100%;
      height: auto;
    }
  }

  .moovaz-logo {
    float: right;
    img {
      margin-left: 10px;
      max-width: 182px;
      height: auto;
    }

    @media screen and (max-width: 991px) {
      margin-bottom: 30px;
    }
  }

  .sub-img-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    @media screen and (max-width: 767px) {
      img {
        max-width: 150px;
        height: auto;
      }

      margin-bottom: 30px;
    }
  }

  .icon-container {
    display: flex;
    flex-direction: column;
    align-items: center;

    button {
      transition: transform 0.3s ease;
      &:hover {
        transform: scale(1.1);
      }
    }

    .icon-text {
      white-space: pre-wrap;
    }
  }
}